body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
  background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}

button {
  cursor: pointer;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bounding-box {
	position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.Tilt {
	background: -webkit-linear-gradient(1deg, #ff5edf 0%, #04c8de 100%);
	background: linear-gradient(89deg, #ff5edf 0%, #04c8de 100%);
}
.form {
	width: 700px;
	background: -webkit-linear-gradient(45deg, #fff 25%,transparent 26%,transparent 75%,#fff 76%),
	-webkit-linear-gradient(135deg, #fff 25%,transparent 26%,transparent 75%,#fff 76%);
	background: linear-gradient(45deg, #fff 25%,transparent 26%,transparent 75%,#fff 76%),
	linear-gradient(-45deg, #fff 25%,transparent 26%,transparent 75%,#fff 76%);
	background-color: #000;
	background-size: 100px 100px;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1; 
}

